import React, { Fragment, useState, useMemo } from "react"
import cl from 'classnames';
import "./index.css"
import throttle from 'lodash.throttle';
import logoPic from '../../images/kg.svg'
import menuIcon from '../../images/icons/menu-icon.svg';
import crossIcon from '../../images/icons/cross.svg';
import Link from '../link/index';
import Container from '../container';
import Button from '../button';

function Logo({ isMain }) {
  return <div className="header__logo-container">
    {isMain
      ? <img src={logoPic} alt="Logo - KG" width="53" height="53" />
      : <Link type="wrapper" href="/">
          <img src={logoPic} alt="Logo - KG" width="53" height="53" />
        </Link>
    }
  </div>
}

function MenuLinks({ onClick }) {
  return <ol>
    <li><Link onClick={onClick} href="/#work">Work</Link></li>
    <li><Link onClick={onClick} href="/#about">About</Link></li>
    <li><Link onClick={onClick} href="/#contacts">Contact</Link></li>
  </ol>
}

function MobileMenu({ isOpen, onClick }) {
  return <Fragment>
    <div className={cl('header__links-mobile', {
      'header__links-mobile--open': isOpen,
    })}>
      <Button type="button" visualType="wrapper" onClick={onClick}>
        {isOpen ? <img src={crossIcon} alt="" /> : <img src={menuIcon} alt="" /> }
      </Button>
    </div>
  </Fragment>
}

function MobileMenuLinks({ setIsOpen }) {
  return <div className={cl('header__mobile-opened-panel')}>
    <Container>
      <MenuLinks onClick={() => setIsOpen(false)} />
    </Container>
  </div>
}

export default function Header({ isMain }) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <header className={cl('header', {
      'header--open': isOpen,
    })}>
      <Container>
        <div className="header__container">
          <Logo isMain={isMain} />

          <nav className={cl('header__links')}>
            <MenuLinks />
          </nav>

          <MobileMenu
            isOpen={isOpen}
            onClick={() => setIsOpen(!isOpen)}
          />
        </div>
      </Container>

      <MobileMenuLinks setIsOpen={setIsOpen} />
    </header>
  )
}
