import React from "react"
import Helmet from 'react-helmet';
import Header from './header/index';
import Footer from './footer/index';

const FAVICONS = [
  { rel: "icon", sizes: "16x16", href: "/favicon-16x16.png" }, // 16x16
  { rel: "icon", sizes: "32x32", href: "/favicon-32x32.png" }, // 32x32
  { rel: "icon", href: "/favicon.svg", type: "image/svg+xml" },
  { rel: "apple-touch-icon", href: "/favicon-apple-touch.png" }, // 180Ã—180
  { rel: "manifest", href: "/manifest.webmanifest" },
];

export default function Layout({ children, location }) {
  const isMain = (location && location.pathname === '/');

  return (
    <div className="layout">
      <Header isMain={isMain} />
      <Helmet link={FAVICONS} />

      <main className="layout__content">
        {children}
      </main>

      <Footer isMain={isMain} />
    </div>
  )
}
