import * as React from "react"
import './index.css';
import cl from 'classnames';

const DEFAULT_TYPE = 'default';

const allowedTypes = new Set([
  DEFAULT_TYPE,
  'wrapper',
]);

export default function Button({ children, className, type = "button", visualType, onClick }) {
  const modType = allowedTypes.has(visualType) ? visualType : DEFAULT_TYPE;

  const mods = {
    [`btn--${modType}`]: modType,
  };

  return (
    <button type={type} className={cl('btn', className, mods)} onClick={onClick}>
      {children}
    </button>
  )
}
